.sidebar{
    flex: 0.2;
}
.sidebar > hr
{
    height: 1px;
    border: 0;
    background-color: lightgrey;
    margin-top: 10px;
    margin-bottom: 10px;
}
.sidebarRow {
    display: flex;
    align-items: center;
    padding: 10px 20px;
}

.SidebarRow__icon {
    color: #606060;
    font-size: large !important;
}

.sidebarRow__title {
    flex: 1;
    margin-left: 20px;
    font-size: 15px;
    font-weight: 12px;
}

.sidebarRow:hover {
    background-color: lightgray;
    cursor: pointer;
}

.sidebarRow:hover > .sidebarRow__icon{
    color: red;
}

.sidebarRow:hover > .sidebarRow__title{
    font-weight:bold ;
    color: #606060;
}

.sidebarRow.selected{
    background-color: lightgray;
}

/* deafut*/
.sidebarRow.selected > .sidebarRow__icon{
    color: red;
}


.sidebarRow.selected > .sidebarRow__title{
    font-weight: bold;
}