.header{
    display:flex;
align-items: center;
justify-content: space-between;
padding: 20px;
position: sticky;
top: 0;
z-index: 100;
background-color: white;
}
.header__logo{
    height:25px;
    object-fit: contain;
    margin-left: 20px;
}
.header__input{
    display:flex;
align-items: center;
width: 40%;
border: 1px solid black;
}

.header__icons{
    display: flex;
    align-items: center;
    margin: 8px;
}
.header__input>input{
    flex: 1;
    border: none;
}
.header__inputButton{
    width: 50px;
    background-color: aliceblue;
    border-left: 1px solid lightgrey;
    color: gray;
}