.app__page {
    display: flex;
    
}
.header{
    display:flex;
align-items: center;
justify-content: space-between;
padding: 20px;
position: -webkit-sticky;
position: sticky;
top: 0;
z-index: 100;
background-color: white;
}
.header__logo{
    height:25px;
    object-fit: contain;
    margin-left: 20px;
}
.header__input{
    display:flex;
align-items: center;
width: 40%;
border: 1px solid black;
}

.header__icons{
    display: flex;
    align-items: center;
    margin: 8px;
}
.header__input>input{
    flex: 1 1;
    border: none;
}
.header__inputButton{
    width: 50px;
    background-color: aliceblue;
    border-left: 1px solid lightgrey;
    color: gray;
}
.recommendedvideos{
    flex: 0.8 1;
    background-color: white ;
    padding: 40px 20px;
    padding-bottom: 0;
   
}
.recommendedvideos > h2{
    margin-left: 5px;
    margin-bottom: 20px;

}
.recommendedVideos__videos {
    display: flex;
    flex-wrap: wrap;
}
.VideoCard {
    width: 300px;
    margin-bottom: 40px;
    padding: 15px;
    
}
.VideoCard__thumbnail {
    width: 300px;
    height: 220px;
}
.videoCard__info { 
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
}
.videoCard__text { 
   
    margin-left: 15px;
    
}
.videoCard__text > h4{ 
   font-size: 14px;
    margin-bottom: 5px;
    
}
.videoCard__text > p{ 
    font-size: 14px;
     color: grey;
     
 }
 .video__avatar {
    width: 30px;
    height: 30px;
 }
.SearchPage {
flex: 0.8 1;
padding: 20px 20px;
background-color: white;
}
.SearchPage__filter { 
    display: flex;
    align-items: center;
    color: #606060;
    font-size: xx-small;
}
.SearchPage__filter > h2 {
    margin-left: 10px;
}
.SearchPage > hr {
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;
}
.ChannelRow {
display: flex;
    align-items: center;
   width: 80%;
}
.ChannelRow__text {
    display: flex;
    flex-direction: column;
}
.ChannelRow__text > p {
    color: #606060;
    font-size: small;
}
.ChannelRow__logo {
    height: 50%;
    width: 40%;
    margin: 50px 70px;
}
.ChannelRow__text > h4 {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.VideoRow {
    display: flex;
    margin-bottom: 30px;
    max-width: 700px;
}
.VideoRow > img {
    object-fit: contain;
    width: 246px;
    height: 138px;
}
.VideoRow__text {
    margin-left: 14px;
    
}
.VideoRow__description {
    margin-top: 20px;
    font-size: 14px;
    color: #606060;
}
.VideoRow__headline {
font-size: 16px;
color: #606060;
}
.VideoRow__subsNumber {
font-weight: bolder;
color: blue;
}
.VideoRow__subs {
background-color: lightgray;
padding: 2px;
}
